.header {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=80);
  -moz-opacity: 0.80;
  -khtml-opacity: 0.8;
  opacity: 0.8;
  position: fixed;
  top: 0;
  width: 100%;
  height: 150px;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  z-index: 1; }

.btn {
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  line-height: 45px;
  position: relative;
  text-transform: uppercase; }

.btn-hover {
  margin: 10px;
  padding: 0 20px;
  border: 1px solid #f7f7f7;
  border-radius: none;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
  border-radius: 0;
  transition: .3s; }

.btn-hover:after {
  position: absolute;
  transition: .3s;
  content: '';
  width: 0;
  left: 50%;
  bottom: 0;
  height: 3px;
  background: #f7f7f7;
  left: 0; }

.btn-hover:hover {
  color: white;
  cursor: pointer; }

.btn-hover:hover:after {
  width: 100%;
  left: 0; }

.header-item {
  padding: 40px; }

.main-wrapper {
  position: relative;
  height: 50vh;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url("../images/back.jpg");
  background-color: grey;
  background-blend-mode: darken; }

.main-item {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  color: red;
  margin-top: 25vh; }

.get-started .btn {
  background: #EDCF49; }

.slider {
  background: #24A69A; }

.main-item {
  color: white;
  text-transform: uppercase; }

.responsive-devices {
  padding: 20px;
  width: 100%;
  background: #EDCF49; }

.responsive-devices img {
  margin: auto; }

.responsive-devices h2 {
  text-align: center; }
